body {
  background-color: $body-background!important;
}
hr {
  border:0;
  display: block;
  height: 1px;
  background: #DBDBDB;
}
.companies-page {
  padding: 0 56px;
}

h5,h4,h3,h2,h1 {
  font-weight: normal;
}

.modal-open {
	position: fixed;
  padding-right: 0!important;
}


@media (max-width: 575px) {

  .content {
    padding: 75px 0 30px 0;
  }
  .companies-page {
    padding: 0 15px;
  }
  .modal-open {
    overflow: hidden;
    padding-right: 0!important;
  }
}

.hide-before-load {
  visibility: hidden;
}
a:focus {
  color: inherit;
  text-decoration: none;
}
a[href^="tel"]{
  text-decoration:none;
}

.owl-item {
  float: left;
}
